.main{
 
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  text-align: center;
  font-size: larger;
  
  
}

.button-29 {
  align-items: center;
  background-image: radial-gradient(100% 100% at 100% 0, #5adaff 0, #5468ff 100%);
  border: 0;
  border-radius: 6px;
  box-shadow: rgba(45, 35, 66, .4) 0 2px 4px,rgba(45, 35, 66, .3) 0 7px 13px -3px,rgba(58, 65, 111, .5) 0 -3px 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: "JetBrains Mono",monospace;
  height: 48px;
  justify-content: center;
  line-height: 1;
  list-style: none;
  overflow: hidden;
  padding-left: 16px;
  padding-right: 16px;
  position: relative;
  text-align: left;
  text-decoration: none;
  transition: box-shadow .15s,transform .15s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow,transform;
  font-size: 18px;
}

.button-29:focus {
  box-shadow: #3c4fe0 0 0 0 1.5px inset, rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
}

.button-29:hover {
  box-shadow: rgba(45, 35, 66, .4) 0 4px 8px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
  transform: translateY(-2px);
}

.button-29:active {
  box-shadow: #3c4fe0 0 3px 7px inset;
  transform: translateY(2px);
}
.title{
  width: 100%;
  padding: 5px;
  border-bottom: 2px black solid;
  box-shadow: 2px 2px 2px black;
}
.search{
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-box{
  height: 3rem;
  width: 16rem;
  margin-right: 1rem;
  border-radius: 5px;
  text-align: center;
  font-size: larger;
}

.search-button{
  height: 3rem;
  width: 6rem;
  border-radius: 5px;
  font-size: larger;
  background-color: #5dbea3;
   cursor: pointer;
 
}

.weather{
  height: 40%;
  margin-top: 2rem;
  margin-left: 0.1rem;
  margin-right: 0.1rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: gray;
  border-radius: 10px;
  background-image: url(../public/night-skyline-wallpaper-25.jpg);
  background-repeat: no-repeat;
  background-size:cover;
  color: white;
  font-family:fantasy;
}
.weather-temp{
  display: flex;
  flex-direction: column;
  align-items:center;
  width: 40%;
  gap: 0.1rem;
}
.weather-details{
  display: flex;
  flex-direction: column;
  gap: 1rem;
}



@media screen and (min-width:576px) {
  .search{
    margin-top: 1.5rem;
  }
  .search-box{
    width: 20rem;
    height: 3rem;
    font-size: larger;


  }
  .search-button{
    width: 9rem;
    height: 3rem;
    font-size: larger;
  }
  .weather{
    width: 50vw;
    height: 500px;
    flex-direction: row;
    font-family:'Courier New', Courier, monospace

  }

}